import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { ArrowLeft, Check, CreditCard, Loader2, Star } from 'lucide-react';
import { toast } from 'sonner';
import axiosInstance from '@/api/axios/axiosInstance';
import { loadStripe } from '@stripe/stripe-js';
import { Elements, CardElement, useStripe, useElements } from '@stripe/react-stripe-js';
import { useNavigate } from 'react-router-dom';

const stripePromise = loadStripe(import.meta.env.VITE_STRIPE_PUBLISHABLE_KEY);

const PaymentForm = ({ selectedPackage, onSuccess }) => {
  const stripe = useStripe();
  const elements = useElements();
  const [processing, setProcessing] = useState(false);
  const [clientSecret, setClientSecret] = useState('');
  const [isFreeTrial, setIsFreeTrial] = useState(false);

  useEffect(() => {
    if (selectedPackage) {
      createPaymentIntent();
    }
  }, [selectedPackage]);

  const createPaymentIntent = async () => {
    try {
      const response = await axiosInstance.post('/api/credits/purchase', {
        packageId: selectedPackage.package_id
      });
      setClientSecret(response.data.data.client_secret);
      setIsFreeTrial(response.data.data.isFreeTrial || false);
    } catch (error) {
      console.error('Error creating payment intent:', error);
      toast.error('Failed to initialize payment');
    }
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    if (!stripe || !elements || !clientSecret) {
      return;
    }

    setProcessing(true);

    try {
      let result;

      if (isFreeTrial) {
        // Handle free trial with setup intent
        const cardElement = elements.getElement(CardElement);

        // First create the payment method
        console.log('ðŸ”„ Creating payment method with card element');
        const { error: pmError, paymentMethod } = await stripe.createPaymentMethod({
          type: 'card',
          card: cardElement,
        });

        if (pmError) {
          console.error('âŒ Payment method creation failed:', pmError);
          toast.error(pmError.message || 'Card validation failed');
          setProcessing(false);
          return;
        }

        console.log('âœ… Payment method created successfully:', paymentMethod.id);

        // Then confirm the setup intent with the payment method ID
        console.log('ðŸ”„ Confirming setup intent with payment method:', paymentMethod.id);
        result = await stripe.confirmSetup({
          clientSecret,
          confirmParams: {
            payment_method: paymentMethod.id
          },
          redirect: 'if_required'
        });
        console.log('âœ… Setup intent confirmation result:', result);

        if (result.error) {
          console.error('Setup failed:', result.error);
          toast.error(result.error.message || 'Card verification failed');
          setProcessing(false);
        } else if (result.setupIntent.status === 'succeeded') {
          toast.success('ðŸŽ‰ Free trial activated! $10 worth of credits added to your account.');

          // Trigger global credit balance update
          window.dispatchEvent(new CustomEvent('creditBalanceUpdate'));

          setTimeout(() => {
            onSuccess(result.setupIntent);
          }, 2000);
        }
      } else {
        // Handle regular payment
        result = await stripe.confirmCardPayment(clientSecret, {
          payment_method: {
            card: elements.getElement(CardElement),
          }
        });

        if (result.error) {
          console.error('Payment failed:', result.error);
          toast.error(result.error.message || 'Payment failed');
          setProcessing(false);
        } else if (result.paymentIntent.status === 'succeeded') {
          toast.success('ðŸŽ‰ Payment successful! Credits added to your account.');

          // Trigger global credit balance update
          window.dispatchEvent(new CustomEvent('creditBalanceUpdate'));

          setTimeout(() => {
            onSuccess(result.paymentIntent);
          }, 2000);
        }
      }
    } catch (error) {
      console.error('Error processing payment:', error);
      toast.error('An error occurred while processing your request');
      setProcessing(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      {/* Card Input Section */}
      <div className="space-y-3">
        <label className="block text-sm font-medium text-gray-700">
          Card Information
        </label>
        <div className="relative p-4 border-2 border-gray-200 rounded-lg focus-within:border-blue-500 focus-within:ring-2 focus-within:ring-blue-200 transition-all">
          {/* Card Brand Icons */}
          <div className="absolute right-3 top-3 flex space-x-1">
            <img src="https://js.stripe.com/v3/fingerprinted/img/visa-729c05c240c4bdb47b03ac81d9945bfe.svg" alt="Visa" className="h-6 w-auto opacity-60" />
            <img src="https://js.stripe.com/v3/fingerprinted/img/mastercard-4d8844094130711885b5e41b28c9848f.svg" alt="Mastercard" className="h-6 w-auto opacity-60" />
          </div>
          <CardElement
            options={{
              style: {
                base: {
                  fontSize: '16px',
                  color: '#374151',
                  fontFamily: '"Inter", system-ui, sans-serif',
                  '::placeholder': {
                    color: '#9CA3AF',
                  },
                  iconColor: '#6B7280',
                },
                invalid: {
                  color: '#EF4444',
                  iconColor: '#EF4444',
                },
              },
              hidePostalCode: false,
              disableLink: true, // This removes the "Autofill Link"
            }}
          />
        </div>
      </div>

      {/* Action Button */}
      <Button
        type="submit"
        disabled={!stripe || processing}
        className="w-full h-12 bg-blue-600 hover:bg-blue-700 text-white font-semibold"
      >
        {processing ? (
          <>
            <Loader2 className="h-5 w-5 mr-2 animate-spin" />
            {isFreeTrial ? 'Verifying Card...' : 'Processing Payment...'}
          </>
        ) : (
          <>
            <CreditCard className="h-5 w-5 mr-2" />
            {isFreeTrial ? 'Claim Free Trial' : `Pay $${selectedPackage?.price_dollars}`}
          </>
        )}
      </Button>
    </form>
  );
};

const CreditPurchasePage = () => {
  console.log('ðŸŽ‰ CreditPurchasePage component loaded');
  const navigate = useNavigate();
  const [packages, setPackages] = useState([]);
  const [selectedPackage, setSelectedPackage] = useState(null);
  const [loading, setLoading] = useState(true);

  // Add early return for testing
  if (loading && packages.length === 0) {
    return (
      <div className="max-w-7xl mx-auto px-4 py-8">
        <div className="text-center">
          <h1 className="text-3xl font-bold text-gray-900 mb-4">ðŸŽ‰ Credit Purchase Page Loaded!</h1>
          <p className="text-gray-600">This page is working correctly.</p>
          <button
            onClick={() => navigate('/credits')}
            className="mt-4 px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
          >
            Back to Credits
          </button>
        </div>
      </div>
    );
  }

  useEffect(() => {
    fetchPackages();
  }, []);

  const fetchPackages = async () => {
    try {
      const response = await axiosInstance.get('/api/credits/packages');
      setPackages(response.data.data.packages);
    } catch (error) {
      console.error('Error fetching packages:', error);
      toast.error('Failed to load credit packages');
    } finally {
      setLoading(false);
    }
  };

  const formatPrice = (cents) => {
    return (cents / 100).toFixed(2);
  };

  const handlePaymentSuccess = () => {
    // Navigate back to credits page
    window.location.href = '/credits';
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center min-h-[400px]">
        <Loader2 className="h-8 w-8 animate-spin text-blue-600" />
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto px-4 py-8">
      {/* Header */}
      <div className="flex items-center gap-4 mb-8">
        <Button
          variant="ghost"
          onClick={() => navigate('/credits')}
          className="flex items-center gap-2"
        >
          <ArrowLeft className="h-4 w-4" />
          Back to Credits
        </Button>
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Purchase Credits</h1>
          <p className="text-gray-600 mt-1">Choose a package and complete your purchase</p>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
        {/* Package Selection */}
        <div className="space-y-6">
          <h2 className="text-xl font-semibold text-gray-900">Select Package</h2>
          <div className="grid grid-cols-1 gap-4">
            {packages.map((pkg) => {
              const isFreeTrial = pkg.package_id === 'free_trial';
              return (
                <Card
                  key={pkg.package_id}
                  className={`cursor-pointer transition-all hover:shadow-md border-2 ${
                    selectedPackage?.package_id === pkg.package_id
                      ? (isFreeTrial ? 'border-green-500 bg-green-50' : 'border-blue-500 bg-blue-50')
                      : 'border-gray-200 hover:border-gray-300'
                  } ${pkg.is_popular ? 'ring-1 ring-blue-400' : ''} ${
                    isFreeTrial ? 'ring-2 ring-green-400 bg-gradient-to-r from-green-50 to-emerald-50' : ''
                  }`}
                  onClick={() => setSelectedPackage(pkg)}
                >
                  <CardContent className="p-6">
                    <div className="flex items-center justify-between">
                      <div className="flex-1">
                        <div className="flex items-center gap-2 mb-2">
                          <h3 className={`text-lg font-semibold ${isFreeTrial ? 'text-green-800' : 'text-gray-900'}`}>
                            {pkg.name}
                          </h3>
                          {isFreeTrial && (
                            <Badge className="bg-green-500 text-white text-xs font-bold">
                              ðŸŽ‰ FREE TRIAL
                            </Badge>
                          )}
                          {pkg.is_popular && !isFreeTrial && (
                            <Badge className="bg-blue-500 text-white text-xs">
                              <Star className="h-3 w-3 mr-1" />
                              Popular
                            </Badge>
                          )}
                        </div>
                        <div className={`mb-1 ${isFreeTrial ? 'text-green-700' : 'text-gray-600'}`}>
                          {pkg.total_credits.toLocaleString()} credits
                        </div>
                        {isFreeTrial ? (
                          <div className="text-sm text-green-600 font-medium">
                            Card required for verification â€¢ One-time offer
                          </div>
                        ) : (
                          <div className="text-sm text-gray-500">
                            ${(pkg.price_cents / 100 / pkg.total_credits).toFixed(4)} per credit
                          </div>
                        )}
                      </div>
                      <div className="text-right">
                        <div className={`text-2xl font-bold ${isFreeTrial ? 'text-green-600' : 'text-blue-600'}`}>
                          {isFreeTrial ? 'FREE' : `$${formatPrice(pkg.price_cents)}`}
                        </div>
                        {isFreeTrial && (
                          <div className="text-sm text-green-600 line-through">
                            $10.00 value
                          </div>
                        )}
                        {selectedPackage?.package_id === pkg.package_id && (
                          <Check className={`h-5 w-5 mt-1 ml-auto ${isFreeTrial ? 'text-green-600' : 'text-blue-600'}`} />
                        )}
                      </div>
                    </div>
                  </CardContent>
                </Card>
              );
            })}
          </div>
        </div>

        {/* Payment Section */}
        <div className="space-y-6">
          <h2 className="text-xl font-semibold text-gray-900">Payment Details</h2>
          {selectedPackage ? (
            <div className="space-y-6">
              {/* Selected Package Summary */}
              <Card className="bg-gray-50 border-gray-200">
                <CardContent className="p-6">
                  <h3 className="font-medium text-gray-900 mb-4">Order Summary</h3>
                  <div className="flex justify-between items-center mb-2">
                    <span className="text-gray-600">{selectedPackage.name}</span>
                    <span className="font-medium">${formatPrice(selectedPackage.price_cents)}</span>
                  </div>
                  <div className="flex justify-between items-center mb-4">
                    <span className="text-gray-600">Credits</span>
                    <span className="font-medium">{selectedPackage.total_credits.toLocaleString()}</span>
                  </div>
                  <div className="border-t pt-4">
                    <div className="flex justify-between items-center">
                      <span className="text-lg font-semibold">Total</span>
                      <span className="text-xl font-bold text-blue-600">
                        ${formatPrice(selectedPackage.price_cents)}
                      </span>
                    </div>
                  </div>
                </CardContent>
              </Card>

              {/* Payment Form */}
              <Card>
                <CardContent className="p-6">
                  <Elements stripe={stripePromise}>
                    <PaymentForm
                      selectedPackage={selectedPackage}
                      onSuccess={handlePaymentSuccess}
                    />
                  </Elements>
                </CardContent>
              </Card>
            </div>
          ) : (
            <Card>
              <CardContent className="p-12 text-center">
                <CreditCard className="h-12 w-12 mx-auto mb-4 text-gray-400" />
                <p className="text-gray-500">Select a package to continue with payment</p>
              </CardContent>
            </Card>
          )}
        </div>
      </div>
    </div>
  );
};

export default CreditPurchasePage;
